@import 'variables.scss';

header {
  grid-template: "header";
  display: flex;
  height: 300px;
  align-items: center;

  h1.title {
    color: $off-white;
  }
}

.wip {
  color: darkorange;
}

.complete {
  color: forestgreen;
}

.tooltip {
  float: left;
  position: relative;
  text-align: center;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 140px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: -1.5rem;
  left: 50%;
  margin-left: -70px;
  opacity: 0;
  transition: opacity 1s;
  &.status {
    top: -3rem;
  }
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.details {
  display: flex;
  flex-direction: row;
  .images {
    display: flex;
    width: 60%;
      img {
        width: 100%;
        object-fit: cover;
    }
  }
  .info {
    width: 50%;
    padding: 2rem;
    .tech {
      display: flex;
      flex-wrap: wrap;
      padding: 2rem 0;
      img {
        max-height: 100px;
        margin: 1rem;
        width: auto;
      }
    }
  }
}

@media #{$medium-and-down} {

  .details {
    flex-wrap: wrap;
    .images {
      max-height: 70vh;
      width: 100%;
    }
    .info {
      width: 100%;

      .tooltip {
        width: 100%!important;
      }
    }
  }

}
